<template>
  <div id="products" @touchstart="hideList">
    <div id="main">
      <p class="title">产品 & 解决方案</p>
      <div class="search-bar">
        <input id="search" class="left" type="text" placeholder="输入产品或解决方案的关键字">
        <div class="search-icon pointer left" @click="search"></div>
      </div>
      <div class="subtitle-bar">
        <div v-for="(item, index) in modeList" v-bind:key="index" class="subtitle left pointer" :class="{'active': index === mode}" @click="modeSwitch(index)">
          <p>{{item}}</p>
        </div>
        <div class="subtitle right pointer">
          <p>筛选</p>
        </div>
      </div>
    </div>
    <div class="main-content">
      <div v-if="!detailStatus && mode === 0" class="main-content-page">
        <div v-for="(item, index) in productList" v-bind:key="index" class="main-content-item left" :class="{'head': index === 0}" @click="seeDetail(index, item)">
          <p>{{item}}</p>
        </div>
      </div>
      <div v-if="!detailStatus && mode === 1" class="main-content-page">
        <div v-for="(item, index) in industryList" v-bind:key="index" class="main-content-item left" :class="{'head': index === 0}" @click="seeDetail(index, item)">
          <p>{{item}}</p>
        </div>
        <div v-for="(item, index) in solutionList" v-bind:key="productList.length + index" class="main-content-item left" :class="{'head': index === 0}" @click="seeDetail(index, item)">
          <p>{{item}}</p>
        </div>
      </div>
      <div v-if="detailStatus" class="main-content-page">
        <div v-for="(item, index) in detailList" v-bind:key="index" class="main-content-item left" :class="{'head': index === 0}" @click="seeProduct(index, item.title)">
          <p class="left">{{item.title}}</p>
          <div v-if="index === 0" class="back pointer left" @click="back"></div>
        </div>
      </div>
      <foot></foot>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import foot from './Foot'
export default {
  name: 'products',
  data () {
    return {
      mode: 0, // 0产品，1解决方案
      detailStatus: false,
      detailList: [],
      modeList: [
        '产品',
        '解决方案'
      ],
      productList: [
        '产品',
        'eLTE行业无线专网',
        'eLTE宽带集群',
        'GSM-R',
        'SDN控制器',
        '安全',
        '传送网',
        '存储',
        '服务器',
        '管理系统',
        '交换机',
        '接入网',
        '路由器',
        '企业云通信',
        '视频监控',
        '数据中心能源',
        '无线局域网',
        '云计算&大数据'
      ],
      industryList: [
        '行业',
        'ISP',
        '电力',
        '公共安全',
        '智慧城市',
        '交通',
        '教育',
        '金融',
        '零售',
        '石油和天然气',
        '医疗',
        '影视传媒',
        '政务',
        '制造'
      ],
      solutionList: [
        '解决方案',
        '业务云化',
        '云数据中心',
        '企业网络',
        '企业无线',
        '企业协同',
        '视频监控与应急调度',
        '企业物联网',
        '网络安全'
      ]
    }
  },
  components: {
    foot
  },
  methods: {
    modeSwitch: function (i) {
      this.mode = i
      this.detailStatus = false
    },
    renderPage: function (i) {
      if (i % 2 === 0) {
        return 'background-color: white;'
      } else {
        return 'background-color: #F0F2F5;'
      }
    },
    hideList: function () {
      this.$root.eventHub.$emit('hide')
    },
    seeDetail: function (index, x) {
      if (index !== 0) {
        this.detailList = []
        this.detailList.push({title: x})
        for (var i = 0; i < 10; i++) {
          this.detailList.push(
            {title: i}
          )
        }
        this.detailStatus = true
      }
    },
    back: function () {
      this.detailStatus = false
    },
    search: function () {
      if ($('#search').val() !== '') {
        alert(1)
      }
    },
    seeProduct: function (i, x) {
      if (i !== 0) {
        this.$root.detailStatus = this.detailStatus
        this.$root.mode = this.mode
        this.$root.detailTitle = this.detailList[0].title
        this.$root.setRecord(this.$route.name, $('#app').scrollTop())
        this.$router.push({path: '/product', query: {productId: x}})
      }
    }
  },
  created () {
    if (this.$root.visitMode === 1) {
      this.detailStatus = this.$root.detailStatus
      this.mode = this.$root.mode
      if (this.detailStatus) {
        this.detailList = []
        this.detailList.push({title: this.$root.detailTitle})
        for (var i = 0; i < 10; i++) {
          this.detailList.push(
            {title: i}
          )
        }
      }
    }
  },
  mounted () {
    if (this.$root.visitMode === 1) {
      this.$root.visitMode = 0
      $('#app').scrollTop(this.$root.getRecord(this.$route.name))
    }
    if (!this.$root.adapt) {
      var temp = this.$root.carousel
      var text = (temp - ($('#main .title').height() + $('#main .search-bar').height() + 24)) / 2
      $('#main').css('height', temp)
      $('#main>p:first-of-type').css('margin-top', text)
      $('#main .subtitle-bar').css('margin-top', text + 18 - $('#main .subtitle-bar').height())
    }
  }
}
</script>

<style scoped>
  #main{
    background-size: cover;
    overflow: hidden;
    position: relative;
    background: crimson;
  }
  #main .title{
    text-align: center;
    color: white;
  }
  .search-bar{
    height: 40px;
    background: white;
    overflow: hidden;
  }
  .search-bar input{
    height: 36px;
    border: 0;
    outline: 0;
    padding: 2px 2px 2px 10px;
    font-size: 14px;
    line-height: 36px;
  }
  .search-icon{
    width: 40px;
    height: 40px;
    background: url("../../resource/assets/search.png") 50% no-repeat;
    background-size: 60%;
  }
  .search-icon:active{
    -webkit-transform: scale(0.92);
    -moz-transform: scale(0.92);
    -ms-transform: scale(0.92);
    transform: scale(0.92);
  }
  .subtitle-bar{
    height: auto;
    overflow: hidden;
  }
  .subtitle.active{
    font-weight: bold;
  }
  .subtitle{
    font-size: 18px;
    color: white;
  }
  .main-content-page{
    overflow: hidden;
  }
  .back{
    width: 40px;
    height: 40px;
    background: url("../../resource/assets/back2.png") 50% no-repeat;
    background-size: 50%;
  }

  @media only screen and (max-device-width: 767px) {
    #main{
      width: 100%;
    }
    #main .title{
      font-size: 22px;
      height: 52px;
    }
    .search-bar{
      width: 300px;
      margin: 0 calc((100% - 300px) / 2);
    }
    .subtitle.left{
      padding: 12px 18px 12px 0;
    }
    .subtitle.right{
      padding: 12px 0;
    }
    .search-bar input{
      width: 248px;
    }
    .subtitle-bar{
      width: calc(100% - 40px);
      margin: 0 20px;
    }
    .main-content{
      width: 100%;
      height: auto;
    }
    .main-content-page{
      padding: 2px 0 8px 0;
    }
    .main-content-item{
      width: calc(100% - 40px);
      height: 40px;
      margin: 0 20px;
      padding: 10px 0;
    }
    .main-content-item:not(:last-of-type), .solution-item:not(:last-of-type){
      border-bottom: 1px solid #f0f2f5;
    }
    .main-content-item p, .solution-item p{
      font-size: 16px;
      line-height: 40px;
    }
    .main-content-item.head p, .solution-item.head p{
      font-size: 18px;
      font-weight: bold;
    }
    .back{
      float: right;
    }
  }

  @media only screen and (min-device-width: 768px) {
    #main .title{
      font-size: 22px;
      margin: 180px 0 50px;
    }
    .search-bar{
      width: 480px;
      margin: 30px calc((100% - 480px) / 2);
    }
    .search-bar input{
      width: 428px;
    }
    .subtitle-bar{
      width: 516px;
      margin: 40px calc((100% - 516px) / 2) 0;
    }
    .subtitle{
      padding: 12px 13px;
    }
    .main-content{
      width: 100%;
      height: auto;
    }
    .main-content-page{
      padding: 30px 0 54px;
    }
    .main-content-item{
      width: calc(50% - 76px);
      height: 40px;
      margin: 10px 18px;
      padding: 0 20px;
      border-bottom: 1px solid #f0f2f5;
    }
    .main-content-item.head{
      width: calc(100% - 76px);
    }
    .main-content-item:not(.head){
      cursor: pointer;
    }
    .main-content-item p{
      font-size: 16px;
      line-height: 40px;
    }
    .main-content-item.head p{
      font-size: 18px;
      font-weight: bold;
    }
    .back{
      margin-left: 10px;
    }
  }

  @media only screen and (min-device-width: 768px) and (max-width: 600px) {
    .main-content-item{
      width: calc(100% - 76px);
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 1094px) {
    .main-content-page{
      width: 1066px;
      margin: 0 calc((100% - 1066px) / 2);
    }
  }

  @media only screen and (min-device-width: 768px) and (min-height: 960px) {
    #main .title{
      margin-top: 210px;
    }
  }
</style>



